import { ImLinkedin, BsGithub } from "../Icons";

const IntroCard = () => {
	return (
		<section className="intro-card">
			<div className="intro-card__top">
				<div
					className="intro-card__cover"
					style={{
						backgroundImage: "url('./images/cover.jpg')",
					}}
				/>
				<div className="intro-card__image">
					<img src="./images/my-img.jpg" alt="Person" />
				</div>
			</div>

			<div className="intro-card__body">
				<p className="intro-card__name">Mosaab Mohamed</p>
				<p className="intro-card__job">Front-End Developer</p>
				<ul className="intro-card__social">
					<li className="intro-card__social-item">
						<a
							href="https://www.linkedin.com/in/mosaab-mohamed-652806bb/"
							target="_blank"
							rel="noreferrer"
						>
							<ImLinkedin />
						</a>
					</li>
					<li className="intro-card__social-item">
						<a
							href="https://github.com/Mosaab-Mohamed"
							target="_blank"
							rel="noreferrer"
						>
							<BsGithub />
						</a>
					</li>
				</ul>
			</div>
			<a className="intro-card__cv" href="./Mosaab_Mohamed-CV.pdf" download>
				Download CV
			</a>
		</section>
	);
};

export default IntroCard;
